import * as React from 'react';
import { Link, graphql } from 'gatsby';

import Bio from '../components/bio';
import Layout from '../components/layout';
import Seo from '../components/seo';
import { CategoryTag } from '../components/common';

const BlogPostTemplate = ({ data: { previous, next, site, markdownRemark: post }, location }) => {
	const siteTitle = site.siteMetadata?.title || `Title`;

	return (
		<Layout location={location} title={siteTitle}>
			<article className="blog-post" itemScope itemType="http://schema.org/Article">
				<header>
					<CategoryTag category={post.frontmatter.category} />
					<h1 itemProp="headline" className="dark:text-white">
						{post.frontmatter.title}
					</h1>
					<p className="mt-0">{post.frontmatter.date}</p>
				</header>
				<section dangerouslySetInnerHTML={{ __html: post.html }} itemProp="articleBody" />
				<hr />
				<footer>
					<Bio />
				</footer>
			</article>
			<nav className="blog-post-nav">
				<ul
					style={{
						display: `flex`,
						flexWrap: `wrap`,
						justifyContent: `space-between`,
						listStyle: `none`,
						padding: 0,
					}}>
					<li>
						{previous && (
							<Link to={previous.fields.slug} rel="prev" className="font-bold underline-offset-4 hover:underline text-blue-primary">
								← {previous.frontmatter.title}
							</Link>
						)}
					</li>
					<li>
						{next && (
							<Link to={next.fields.slug} rel="next" className="font-bold underline-offset-4 hover:underline text-blue-primary">
								{next.frontmatter.title} →
							</Link>
						)}
					</li>
				</ul>
			</nav>
		</Layout>
	);
};

export const Head = ({ data: { markdownRemark: post } }) => {
	return <Seo title={post.frontmatter.title} description={post.frontmatter.description || post.excerpt} />;
};

export default BlogPostTemplate;

export const pageQuery = graphql`
	query BlogPostBySlug($id: String!, $previousPostId: String, $nextPostId: String) {
		site {
			siteMetadata {
				title
			}
		}
		markdownRemark(id: { eq: $id }) {
			id
			excerpt(pruneLength: 160)
			html
			frontmatter {
				title
				date(formatString: "MMMM DD, YYYY")
				description
				category
			}
		}
		previous: markdownRemark(id: { eq: $previousPostId }) {
			fields {
				slug
			}
			frontmatter {
				title
			}
		}
		next: markdownRemark(id: { eq: $nextPostId }) {
			fields {
				slug
			}
			frontmatter {
				title
			}
		}
	}
`;
